"use client";

import React, { useState, useMemo } from "react";
import { DoughnutChart, InputGroup } from "../CalculatorUtils";
import { formatRupee } from "../../UI/Utility";

const InvestmentCalculator = () => {
    const [mode, setMode] = useState("sip");

    // SIP states
    const [monthlyInvestment, setMonthlyInvestment] = useState("100");

    // Lumpsum state
    const [lumpsumAmount, setLumpsumAmount] = useState("10000");

    // Common states
    const [rate, setRate] = useState("12");
    const [years, setYears] = useState("10");

    const R = Number(rate);
    const Y = Number(years);

    const {
        totalInvested,
        totalEarnings,
        calculatedFV,
    } = useMemo(() => {
        if (R <= 0 || Y <= 0) {
            return { totalInvested: 0, totalEarnings: 0, calculatedFV: 0 };
        }

        const annualRate = R / 100;

        // ---------------- SIP (Groww style simulation)
        if (mode === "sip") {
            const P = Number(monthlyInvestment);
            if (P <= 0) {
                return { totalInvested: 0, totalEarnings: 0, calculatedFV: 0 };
            }

            const monthlyRate = Math.pow(1 + annualRate, 1 / 12) - 1;
            const months = Y * 12;

            let balance = 0;

            for (let m = 1; m <= months; m++) {
                balance += P;               // SIP at beginning
                balance *= 1 + monthlyRate; // monthly growth
            }

            const invested = P * months;
            const finalValue = Math.floor(balance);
            const earnings = finalValue - invested;

            return {
                totalInvested: invested,
                totalEarnings: earnings > 0 ? earnings : 0,
                calculatedFV: finalValue,
            };
        }

        // ---------------- LUMPSUM (Groww style)
        const L = Number(lumpsumAmount);
        if (L <= 0) {
            return { totalInvested: 0, totalEarnings: 0, calculatedFV: 0 };
        }


        const fv = L * Math.pow(1 + annualRate, Y);
        const finalValue = Math.floor(fv);
        const earnings = finalValue - L;

        return {
            totalInvested: L,
            totalEarnings: earnings > 0 ? earnings : 0,
            calculatedFV: finalValue,
        };
    }, [mode, monthlyInvestment, lumpsumAmount, R, Y]);

    return (
        <div className="bg-[#0c111d] p-8 rounded-xl border border-slate-800 shadow-2xl text-white">
            {/* HEADER */}
            <div className="flex justify-between items-center mb-8">
                <h3 className="text-2xl font-bold">
                
                </h3>

                {/* MODE TOGGLE */}
                <div className="flex rounded-lg border border-slate-700 overflow-hidden">
                    <button
                        onClick={() => setMode("sip")}
                        className={`px-4 py-2 text-sm font-semibold ${mode === "sip"
                                ? "bg-emerald-500 text-black"
                                : "bg-transparent text-slate-300"
                            }`}
                    >
                        SIP
                    </button>
                    <button
                        onClick={() => setMode("lumpsum")}
                        className={`px-4 py-2 text-sm font-semibold ${mode === "lumpsum"
                                ? "bg-emerald-500 text-black"
                                : "bg-transparent text-slate-300"
                            }`}
                    >
                        Lumpsum
                    </button>
                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
                {/* INPUTS */}
                <div className="lg:col-span-2 space-y-8">
                    {mode === "sip" ? (
                        <InputGroup
                            label="Monthly Investment"
                            value={monthlyInvestment}
                            setter={setMonthlyInvestment}
                            min={100}
                            max={1000000}
                            step={100}
                            unit="₹"
                            accentColor="text-emerald-400 rounded-lg"
                        />
                    ) : (
                        <InputGroup
                            label="Lumpsum Investment"
                            value={lumpsumAmount}
                            setter={setLumpsumAmount}
                            min={1000}
                            max={10000000}
                            step={1000}
                            unit="₹"
                            accentColor="text-emerald-400 rounded-lg"
                        />
                    )}

                    <InputGroup
                        label="Expected Annual Return"
                        value={rate}
                        setter={setRate}
                        min={1}
                        max={30}
                        step={0.1}
                        unit="%"
                        accentColor="text-emerald-400 rounded-5xl"
                    />

                    <InputGroup
                        label="Time Period"
                        value={years}
                        setter={setYears}
                        min={1}
                        max={50}
                        unit="Yrs"
                        accentColor="text-emerald-400"
                    />
                </div>

                {/* CHART */}
                <div className="lg:col-span-1 flex justify-center">
                    <DoughnutChart
                        invested={totalInvested}
                        returns={totalEarnings}
                        investedColor="#10b981"
                        returnsColor="#3b82f6"
                    />
                </div>
            </div>

            {/* RESULT */}
            <div className="mt-12 pt-8 border-t border-slate-800 grid grid-cols-1 md:grid-cols-3 gap-8">
                <div>
                    <p className="text-slate-400 text-sm mb-1">Total Invested</p>
                    <p className="text-2xl font-semibold">
                        {formatRupee(totalInvested)}
                    </p>
                </div>

                <div>
                    <p className="text-slate-400 text-sm mb-1">Est. Returns</p>
                    <p className="text-2xl font-semibold text-blue-400">
                        {formatRupee(totalEarnings)}
                    </p>
                </div>

                <div className="md:text-right">
                    <p className="text-slate-400 text-sm mb-1">Total Value</p>
                    <p className="text-3xl font-bold text-emerald-400">
                        {formatRupee(calculatedFV)}
                    </p>
                </div>
            </div>
        </div>
    );
};

export default InvestmentCalculator;
